
import { mapGetters, mapActions, mapMutations } from 'vuex';
import { api } from '@/utils/api.js';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
nvabarData: {
showCapsule: 1,
title: '返利',
},
page: 0,
listObj: [],
recordType: 1,
show_bottom: false
}
},
created() {
this.recordType = this.$route.query.recordType;
},
mounted() {
let recordType = this.recordType;
if(recordType == 1){
this.nvabarData.title = '推广365返利';
}
if(recordType == 2){
this.nvabarData.title = '线下店铺推广返利';
}
if(recordType == 3){
this.nvabarData.title = '推广商品返利';
}
this.fanli_list();
},
methods: {
async fanli_list(){
let recordType = this.recordType;

var red_type='';
if(recordType == 1){
red_type = 5;
}
if(recordType == 2){
red_type = 2;
}
if(recordType == 3){
red_type = 3;
}
var page = this.page;
page++;
let data = {
plat: 3,
account: this.account,
token: this.token,
red_type,
page
}
const [err, res_data] = await api.rebate_record(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}
if(res_data.code == 2000){
var resList = res_data.data.list;
var total_count = res_data.data.total_count;
var listObj = this.listObj;
var page = this.page;
page++;
var newListObj = listObj.concat(resList);
this.listObj = newListObj;
this.page = page;
this.total_count = total_count;
}
}
},
computed: {
...mapGetters(['user', 'account', 'token'])
}
}



